.main__editor, main__history {
  margin-bottom: 120px; }

.main__editor-img {
  flex-shrink: 0;
  margin-right: 50px; }

.main__editor-name {
  margin-bottom: 16px;
  font-size: 24px;
  line-height: 36px;
  color: #D54113; }

.partners {
  background-color: #FAFAFA; }

.partners__menu {
  display: flex;
  margin-bottom: 64px;
  padding: 80px 50px 24px 0; }
  @media screen and (max-width: 767px) {
    .partners__menu {
      flex-direction: column;
      max-width: 100%;
      padding: 40px 0 12px 0; } }

.partners__name {
  position: relative;
  font-family: "PT Sans Narrow", "Arial", sans-serif;
  font-size: 36px;
  line-height: 47px;
  text-transform: uppercase;
  color: #4F4F4F;
  transition: 0.5s;
  cursor: pointer; }
  .partners__name:first-child {
    padding-right: 20px;
    border-right: 1px solid #D54113; }
  .partners__name:last-child {
    padding-left: 20px; }
  .partners__name--active {
    font-weight: bold;
    color: #D54113; }
  .partners__name:hover {
    opacity: 0.8; }
  @media screen and (max-width: 1365px) {
    .partners__name {
      font-size: 30px; } }
  @media screen and (max-width: 767px) {
    .partners__name {
      margin-bottom: 20px;
      font-size: 22px;
      line-height: 30px; } }

.partners__list {
  display: none;
  flex-wrap: wrap;
  padding-bottom: 110px;
  margin: 0 -20px; }
  .partners__list.active {
    display: flex; }
  @media screen and (max-width: 767px) {
    .partners__list {
      padding-bottom: 40px; } }

.partners__item {
  display: flex;
  align-items: center;
  width: 20%;
  margin-bottom: 80px;
  padding: 0 20px; }
  @media screen and (max-width: 767px) {
    .partners__item {
      width: 50%;
      margin-bottom: 25px; } }

.footer {
  padding-top: 80px;
  background-color: #F4F4F4; }

.footer__box {
  display: flex;
  justify-content: space-between; }

.footer__list {
  margin: 0;
  padding: 0; }

.footer__item {
  margin-bottom: 20px; }

.footer__link {
  font-family: "Work Sans", "Roboto", "Arial", sans-serif;
  font-size: 20px;
  line-height: 23px;
  color: #525252;
  text-decoration: none;
  transition: 0.5s; }
  .footer__link:hover {
    opacity: 0.7; }

.footer__flex {
  display: flex;
  justify-content: space-between;
  margin-bottom: 80px; }
  @media screen and (max-width: 1023px) {
    .footer__flex {
      flex-direction: column; } }

.footer__block {
  width: 28%; }
  @media screen and (max-width: 1023px) {
    .footer__block {
      width: 100%; } }
  @media screen and (max-width: 1023px) {
    .footer__block:first-child {
      margin-bottom: 30px; } }

.footer__heading {
  margin-bottom: 16px;
  font-size: 24px;
  line-height: 31px;
  color: #8D848A; }

.footer__text {
  font-size: 16px;
  line-height: 24px; }
  .footer__text--first {
    min-height: 160px; }
    @media screen and (max-width: 1023px) {
      .footer__text--first {
        min-height: auto; } }
  .footer__text--second {
    margin-bottom: 20px; }

.footer__mail {
  font-family: "Work Sans", "Roboto", "Arial", sans-serif;
  font-size: 16px;
  line-height: 24px;
  color: #D54113;
  text-decoration: none;
  transition: 0.5s; }
  .footer__mail:hover {
    opacity: 0.7; }

.footer__license {
  margin-bottom: 12px;
  font-size: 12px;
  line-height: 15px; }

.footer__right {
  font-family: "Roboto", "Arial", sans-serif;
  padding-bottom: 40px;
  font-size: 12px;
  line-height: 18px;
  color: #5A5A5A; }
